import React, { useState } from "react";
import { Formik, Field, Form, ErrorMessage } from "formik";
import * as Yup from "yup";
import "./VacancyForm.css";
import Calendar from "./icons/Calendar.png";
import Swich from "./icons/Swich.png";
import SwichOpen from "./icons/SwichOpen.png";
import { NumericFormat } from "react-number-format";

//  
const validationSchema = Yup.object({
  title: Yup.string().required(" "),
  department: Yup.string().required(" "),
  operationDate: Yup.date().required("  "),

  gender: Yup.string().required(" "),
  salaryFrom: Yup.string().required("  "),
  region: Yup.string().required(" "),
  address: Yup.string().required(
    "  . ,  , 31"
  ),
  experience: Yup.string().required("  "),
  workSchedule: Yup.string().required("  "),
  employmentType: Yup.string().required("  "),
  netSalary: Yup.string().required("   "),
});

const VacancyForm = ({ onSubmit, vacancy, onCancel }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [arrowIcon, setArrowIcon] = useState(Swich); //   
  const isEdit = Boolean(vacancy);
  const handleToggle = () => {
    setIsOpen((prevState) => !prevState);
    setArrowIcon((prevState) => (prevState === Swich ? SwichOpen : Swich)); //   
  };

  const handleSelectChange = (e, setFieldValue) => {
    setFieldValue(e.target.name, e.target.value);
    setArrowIcon(Swich);
    setIsOpen(false);
  };

  const handleBlur = (e) => {
    if (!e.target.value) {
      setArrowIcon(Swich); //     
    }
    setIsOpen(false); //  
  };
  const handleCancelClick = () => {
    onCancel();
  };

  return (
    <div className="vacancy-form">
      <h2 className="zag1">
        {isEdit ? "  " : "  "} <u></u>
      </h2>

      <Formik
        initialValues={{
          title: vacancy?.title || "",
          department: vacancy?.department || "",
          operationDate: vacancy?.operationDate || "",
          plannedDate: vacancy?.plannedDate || "",
          gender: vacancy?.gender || "",
          region: vacancy?.region || "",
          address: vacancy?.address || "",
          salaryFrom: vacancy?.salaryFrom || "",
          salaryTo: vacancy?.salaryTo || "",
          experience: vacancy?.experience || "",
          metro: vacancy?.metro || "",
          workSchedule: vacancy?.workSchedule || "",
          employmentType: vacancy?.employmentType || "",
          netSalary: vacancy?.netSalary || "",
        }}
        enableReinitialize={true}
        validationSchema={validationSchema}
        onSubmit={(values, { resetForm }) => {
          const newVacancy = {
            ...values,
            id: isEdit ? vacancy.id : new Date().toISOString(),
          };

          onSubmit(newVacancy);
          resetForm();
        }}
      >
        {({ setFieldValue, resetForm, errors, touched }) => (
          <Form>
            <div className="vacancy-form-body">
              <div className="company-row">
                <div className="company-group">
                  <label htmlFor="title">
                     <span className="req">*</span>
                  </label>
                  <Field
                    type="text"
                    id="title"
                    name="title"
                    className={`custom-input ${
                      errors.title && touched.title
                        ? "error"
                        : touched.title
                        ? "success"
                        : ""
                    }`}
                  />
                  <ErrorMessage
                    name="title"
                    component="div"
                    className="error"
                  />
                </div>

                <div className="company-group">
                  <label htmlFor="department">
                    <span className="req">*</span>
                  </label>
                  <Field
                    type="text"
                    id="department"
                    name="department"
                    className={`custom-input ${
                      errors.department && touched.department
                        ? "error"
                        : touched.department
                        ? "success"
                        : ""
                    }`}
                  />
                  <ErrorMessage
                    name="department"
                    component="div"
                    className="error"
                  />
                </div>
              </div>

              <div className="date-row">
                <div>
                  <label htmlFor="operationDate">
                     <span className="req">*</span>
                  </label>
                  <div className="date-input-wrapper">
                    <Field
                      type="date"
                      id="operationDate"
                      name="operationDate"
                      className={`custom-input ${
                        errors.operationDate && touched.operationDate
                          ? "error"
                          : touched.operationDate
                          ? "success"
                          : ""
                      }`}
                    />
                    <img
                      src={Calendar}
                      alt=""
                      className="calendar-icon"
                      onClick={() =>
                        document.getElementById("operationDate").showPicker()
                      }
                    />
                  </div>
                  <ErrorMessage
                    name="operationDate"
                    component="div"
                    className="error"
                  />
                </div>

                <div>
                  <label htmlFor="plannedDate">
                      <span className="req">*</span>
                  </label>
                  <div className="date-input-wrapper">
                    <Field
                      type="date"
                      id="plannedDate"
                      name="plannedDate"
                      className={`custom-date-input custom-input ${
                        errors.plannedDate && touched.plannedDate ? "error" : ""
                      }`}
                    />
                    <img
                      src={Calendar}
                      alt=""
                      className="calendar-icon"
                      onClick={() =>
                        document.getElementById("plannedDate").showPicker()
                      }
                    />
                  </div>
                  <ErrorMessage
                    name="plannedDate"
                    component="div"
                    className="error"
                  />
                </div>
              </div>

              <div>
                <label>
                  <span className="req">*</span>
                </label>
                <div className="radio-group-sex">
                  <Field type="radio" id="male" name="gender" value="" />
                  <label htmlFor="male"></label>

                  <Field
                    type="radio"
                    id="female"
                    name="gender"
                    value=""
                  />
                  <label htmlFor="female"></label>
                  <ErrorMessage
                    name="gender"
                    component="div"
                    className="error"
                  />
                </div>
              </div>

              <div>
                <label>
                  <span className="req">*</span>
                </label>
                <div className="salary-options">
                  <Field
                    type="radio"
                    id="salaryNetHands"
                    name="netSalary"
                    value=" "
                  />
                  <label htmlFor="salaryNetHands"> </label>

                  <Field
                    type="radio"
                    id="salaryPreTax"
                    name="netSalary"
                    value="  "
                  />
                  <label htmlFor="salaryPreTax">  </label>
                </div>

                <ErrorMessage
                  name="netSalary"
                  component="div"
                  className="error"
                />
              </div>

              <div>
                <div className="salary-range">
                  <Field
                    name="salaryFrom"
                    render={({ field, form }) => (
                      <NumericFormat
                        {...field}
                        placeholder=""
                        thousandSeparator={true}
                        allowNegative={false}
                        isNumericString
                        customInput="input"
                        className={`custom-date-input custom-input ${
                          form.errors.salaryFrom && form.touched.salaryFrom
                            ? "error"
                            : touched.salaryFrom
                            ? "success"
                            : ""
                        }`}
                      />
                    )}
                  />
                  <Field
                    name="salaryTo"
                    render={({ field, form }) => (
                      <NumericFormat
                        {...field}
                        placeholder=""
                        thousandSeparator={true}
                        allowNegative={false}
                        isNumericString
                        customInput="input"
                        className="custom-date-input custom-input"
                      />
                    )}
                  />
                </div>
                <div>
                  <ErrorMessage
                    name="salaryFrom"
                    component="div"
                    className="error"
                  />
                </div>
              </div>

              <div className="address-inputs-row">
                <div className="address-input-group">
                  <label htmlFor="region">
                    <span className="req">*</span>
                  </label>
                  <Field
                    type="text"
                    id="region"
                    name="region"
                    className={`custom-input ${
                      errors.region && touched.region
                        ? "error"
                        : touched.region
                        ? "success"
                        : ""
                    }`}
                  />

                  <ErrorMessage
                    name="region"
                    component="div"
                    className="error"
                  />
                </div>

                <div className="address-input-group">
                  <label htmlFor="address">
                    <span className="req">*</span>
                  </label>
                  <Field
                    type="text"
                    id="address"
                    name="address"
                    className={`long-input custom-input ${
                      errors.address && touched.address
                        ? "error"
                        : touched.address
                        ? "success"
                        : ""
                    }`}
                  />
                  <ErrorMessage
                    name="address"
                    component="div"
                    className="error"
                  />
                </div>
                <div className="address-input-group">
                  <label htmlFor="metro"> , </label>
                  <Field type="text" id="metro" name="metro" />
                  <ErrorMessage
                    name="metro"
                    component="div"
                    className="error"
                  />
                </div>
              </div>
              <div className="address-inputs-row">
                <div className="experience-group">
                  <label htmlFor="experience">
                     <span className="req">*</span>
                  </label>
                  <Field
                    type="text"
                    id="experience"
                    name="experience"
                    className={`custom-input ${
                      errors.experience && touched.experience
                        ? "error"
                        : touched.experience
                        ? "success"
                        : ""
                    }`}
                  />
                  <ErrorMessage
                    name="experience"
                    component="div"
                    className="error"
                  />
                </div>

                <div className="schedule-group">
                  <label htmlFor="workSchedule">
                     <span className="req">*</span>
                  </label>
                  <div className="select-wrapper">
                    <Field
                      as="select"
                      id="workSchedule"
                      name="workSchedule"
                      className={`custom-select ${
                        errors.workSchedule && touched.workSchedule
                          ? "error"
                          : ""
                      }`}
                      onFocus={handleToggle}
                      onBlur={handleBlur}
                      onChange={(e) => handleSelectChange(e, setFieldValue)}
                    >
                      <option value="" className="placeHS">
                        
                      </option>
                      <option value="fullTime"> </option>
                      <option value="partTime"> 5/2</option>
                      <option value="remote"> 2/2</option>
                    </Field>

                    <img
                      src={arrowIcon}
                      alt=""
                      className="select-icon"
                    />
                  </div>

                  <ErrorMessage
                    name="workSchedule"
                    component="div"
                    className="error"
                  />
                </div>

                <div className="employment-type-options">
                  <label>
                     <span className="req">*</span>
                  </label>
                  <div className="type-radio-group">
                    <Field
                      type="radio"
                      id="fullTime"
                      name="employmentType"
                      value=" "
                    />
                    <label htmlFor="fullTime"> </label>

                    <Field
                      type="radio"
                      id="partTime"
                      name="employmentType"
                      value=" "
                    />
                    <label htmlFor="partTime"> </label>

                    <Field
                      type="radio"
                      id="project"
                      name="employmentType"
                      value=" "
                    />
                    <label htmlFor="project"></label>

                    <ErrorMessage
                      name="employmentType"
                      component="div"
                      className="error"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="form-actions">
              <button type="submit">
                {isEdit ? "" : ""}
              </button>

              <button
                type="button"
                onClick={isEdit ? handleCancelClick : resetForm}
              >
                {isEdit ? "" : ""}
              </button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default VacancyForm;
